
.footer {
	position: fixed;
	left:0;
	bottom: 0;
	width:100%;
	height:4rem;
	background-color: #51494e;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}
.footer a {
	-webkit-box-flex:1;
	    -ms-flex:1;
	        flex:1;
	height:100%;
	line-height: 6.2rem;
	color:#fff;
	font-size: 1rem;
	text-align: center;
}
.footer a:nth-child(1) {
	background: url("/static/img/homeIco.png") no-repeat;
	background-size:2rem 2rem;
	background-position:center 0.4rem;
}
.footer a:nth-child(2) {
	background: url("/static/img/categoryIco.png") no-repeat;
	background-size:2rem 2rem;
	background-position:center 0.4rem;
}
.footer a:nth-child(3) {
	background: url("/static/img/searchIco.png") no-repeat;
	background-size:2rem 2rem;
	background-position:center 0.4rem;
}
.footer a:nth-child(4) {
	background: url("/static/img/moreIco.png") no-repeat;
	background-size:2rem 2rem;
	background-position:center 0.4rem;
}
#footer .active {
	background-color: #d01179 ;
}



