
.popBox {
	position: fixed;
	top:0;
	left:0;
	width:100%;
	height:100%;
	background-color: rgba(0,0,0,.6);
}
.tipsWindow {
	position: absolute;
	top:0;
	right:0;
	bottom: 0;
	left:0;
	margin: auto;
	background-color: #e35386;
	color: #fff;
	text-align: center;
	width:43.75%;
	height:8rem;
	border-radius: 1rem;
	font-size: 2rem;
}
.subTipsWindow1 {
	height:4rem;
	line-height: 5rem;
}
.subTipsWindow2 {
	height:4rem;
	line-height: 3rem;
}



